@import "./components/StepsProgress/style.scss";
@import "./components/steps/AssetUrl/style.scss";
@import "./components/Navigation/style.scss";
@import "./components/AssetAvatar/style.scss";
@import "./components/steps/ConnectWallet/style.scss";
@import "./components/steps/Ownership/style.scss";
@import "./components/steps/SendTweet/style.scss";
@import "./components/steps/StartStep/style.scss";
@import "./components/Error/style.scss";
@import "./components/Success/style.scss";
@import "./components/VerifiedAsset/style.scss";
@import "./components/Verified/style.scss";

.verify {
  padding-top: 70px;
  padding-bottom: 100px;
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
  &-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 10;
  }
  &-overlay {
    background: linear-gradient(
      242.36deg,
      rgba(0, 0, 0, 0.95) 12.15%,
      rgba(0, 0, 0, 0.2) 88.21%
    );
    opacity: 0.8;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1;
  }

  &-bg {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
  }
  .step-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-title {
      font-size: 1.875rem;
      font-family: $main-font;
      font-weight: 700;
      margin-bottom: 15px;
    }
    &-subtitle {
      font-size: 1.25rem;
      font-family: $main-font;
      font-weight: bold;
      color: rgba(255, 255, 255, 0.5);
      text-align: center;
      margin-bottom: 70px;
    }
  }
  .step {
    display: flex;
    width: 820px;
    height: 310px;
    align-items: center;
    border-radius: 20px;
    background: rgba(0, 0, 0, 0.3);
    border: 2px solid rgba(255, 255, 255, 0.2);
    &-content {
      height: calc(100% - 60px);
      margin-right: 30px;
      position: relative;
      flex: 1;
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    input {
      border-radius: 10px;
      background: rgba(255, 255, 255, 0.2);
      border: 2px solid #ffffff;
      height: 58px;
      width: 100%;
      text-indent: 30px;
      font-size: 1rem;
      font-weight: 500;
      font-family: $main-font;
      color: white;
      margin-bottom: 30px;
      @extend .overflow-text;
    }
  }
}

@media only screen and (max-width: 1500px) {
  .steps-navigation {
    margin-top: 50px;
  }
  .verify {
    .step-container {
      &-subtitle {
        margin-bottom: 50px;
      }
    }
  }
}

@media only screen and (max-width: 870px) {
  .verify {
    overflow-x: hidden;
    padding-top: 50px;
    .verified {
      flex-direction: column;
      width: calc(100% - 40px);
      &-proof {
        width: 100%;
      }
      &-url {
        .button {
          section {
            padding: 0px 10px;
          }
        }
      }

      &-twitter {
        width: calc(100% - 40px);
        &-bg {
          height: 80px;
        }
        #verified-twitter-asset {
          top: 60px;
        }
        &-info {
        }
        p {
          margin-bottom: 10px;
        }
      }
      &-left {
        width: 100%;
        order: 2;
        z-index: 10;
        align-items: center;
        display: flex;
        flex-direction: column;
        .certificate {
          width: 983px;
          height: 540px;
          zoom: 0.3;
          margin: 0px;
        }
      }
      &-right {
        width: 100%;
        height: 400px;
        margin-left: 0px;
        &-arrow {
          display: none;
        }
        &-bottom {
          height: 140px;
          width: 300px;
          left: 50%;
          transform: translate(-50%);
          bottom: -60px;
        }
        &-frame {
          width: 480px;
        }
      }
    }
    .step-container {
      width: calc(100% - 50px);
      &-title {
        font-size: 1.5rem;
      }
      &-subtitle {
        font-size: 1.2rem;
      }
    }

    .steps-progress {
      margin-bottom: 50px;
    }
    .start-step {
      width: 100%;
    }
    .step {
      width: 100%;
      flex-direction: column;
      height: auto;
      padding: 20px;
      &-content {
        margin: 0px;
        width: 100%;
        .button {
          margin-bottom: 30px;
        }
      }
      .send-tweet-varified-content {
        padding-top: 0px;
        padding-bottom: 0px;
      }
      .verified-asset {
        width: 170px;
        height: 170px;
        margin-left: 0px;
        margin-bottom: 20px;
        .lottie {
          width: 100px;
          height: 100px;
        }
        #asset-img {
          width: 129px;
          height: 129px;
          img {
            border-radius: 0px;
          }
        }
      }

      &-asset {
        width: 150px;
        height: 150px;
        margin-left: 0px;
        margin-bottom: 30px;
      }
    }
    .send-tweet {
      .twitter-account {
        padding-top: 40px;
        padding-bottom: 40px;
      }
    }
  }
}
